<template>
  <aside class="GlobalSideBar">
    <div class="Card GlobalWrite"></div>
    <div class="Card GlobalWrite"></div>
    <div class="Card GlobalWrite"></div>
    <div class="Card GlobalWrite"></div>
    <div
      class="Card GlobalWrite"
      :style="getFixedStyle"
      ref="fixedBlock">固定
    </div>
  </aside>
</template>

<script>
  export default {
    name: "AsideDetail",
    data() {
      return {
        isFixed: false,
        offsetTop: 0,
        scrollTop: 0,
      }
    },
    computed: {
      // 计算属性的 getter
      getFixedStyle() {
        if (this.isFixed) {
          // `this` 指向 vm 实例
          let top = this.scrollTop - this.offsetTop;
          return 'top: ' + top + 'px;width: 300px;'
        } else {
          return {}
        }
      }
    },
    mounted() {
      this.offsetTop = this.$refs.fixedBlock.getBoundingClientRect().top;
      window.addEventListener('scroll', this.handleScroll);
      this.$once('hook:beforeDestroy', () => {
        window.removeEventListener('scroll', this.handleScroll);
      });
      window.addEventListener("beforeunload", () => {
        window.scrollTo(0, 0);
      });
    },
    methods: {
      handleScroll() {
        const scrollTop = document.body.scrollTop || document.documentElement.scrollTop || window.pageYOffset;
        if (scrollTop > this.offsetTop) {
          this.isFixed = true;
          this.scrollTop = scrollTop
        } else {
          this.isFixed = false
        }
      },
    },
  }
</script>

<style lang="less" scoped>
  .GlobalSideBar {
    width: 300px;
    font-size: 14px;
    background: lightpink;

    .Card {
      margin-bottom: 10px;
      background: #fff;
      overflow: hidden;
      border-radius: 2px;
      box-shadow: 0 1px 3px rgba(26, 26, 26, 0.1);
      box-sizing: border-box;

      &.GlobalWrite {
        position: relative;
        display: flex;
        height: 188px;
        flex-wrap: wrap;
        overflow: visible;
        background: lightblue;
      }
    }
  }
</style>