<template>
  <aside ref="fixedBlock" class="aside-container" :style="getFixedStyle">
    <div class="item-overview"></div>
    <div class="item-overview"></div>
    <div class="item-overview"></div>
  </aside>
</template>

<script>
  export default {
    mounted() {
      this.offsetTop = this.$refs.fixedBlock.getBoundingClientRect().top;
      // console.log('---->', this.$refs.fixedBlock.getBoundingClientRect());
      window.addEventListener('scroll', this.handleScroll);
      this.$once('hook:beforeDestroy', () => {
        window.removeEventListener('scroll', this.handleScroll);
      });
      window.addEventListener("beforeunload", () => {
        window.scrollTo(0, 0);
      });
    },
    methods: {
      handleScroll() {
        //console.log('handleScroll', this.offsetTop);
        const scrollTop = document.body.scrollTop || document.documentElement.scrollTop || window.pageYOffset;
        if (scrollTop > this.offsetTop) {
          this.isFixed = true;
          this.scrollTop = scrollTop;
        } else {
          this.isFixed = false
        }
      }
    },
    computed: {
      // 计算属性的 getter
      getFixedStyle() {
        if (this.isFixed) {
          let top = this.scrollTop - this.offsetTop;
          return 'top: ' + top + 'px;'
        } else {
          return {}
        }
      }
    },
    data() {
      return {
        isFixed: false,
        offsetTop: 0,
        scrollTop: 0,
      }
    },
    name: "AsideDetailLeft"
  }
</script>

<style lang="less" scoped>
  .aside-container {
    position: relative;
    width: 120px;
    height: auto;
    margin-right: 10px;
    background: lightsteelblue;

    .item-overview {
      width: 100%;
      height: 200px;
      margin-bottom: 10px;
      background: lightgreen;
    }
  }


  @media screen and (max-width: 1300px) {
    .aside-container {
      display: none;
    }
  }
</style>